<template>
  <div>
    <div class="page-title">Slider </div>
    <p>/</p>

    <div class="page-sub-title">
       v-model 
      <br> range  disabled 
      <br>value value 2
    </div>
    <Slider v-model="value1"></Slider>
    <Slider v-model="value2" range></Slider>
    <Slider v-model="value3" range disabled></Slider>

    <div class="page-sub-title"> step </div>
    <Slider v-model="value4" :step="10"></Slider>
    <Slider v-model="value5" :step="10" range></Slider>

    <div class="page-sub-title"> show-stops  step </div>
    <Slider v-model="value6" :step="10" show-stops></Slider>
    <Slider v-model="value7" :step="10" show-stops range></Slider>

    <div class="page-sub-title"> show-input </div>
    <Slider v-model="value8" show-input></Slider>

    <div
      class="page-sub-title"
    >Slider  tip-format Tooltip  tip-format  null Tooltip</div>
    <Slider v-model="value9" :tip-format="format"></Slider>
    <Slider v-model="value10" :tip-format="hideFormat"></Slider>
  </div>
</template>
<script>
export default {
  data() {
    return {
      value1: 25,
      value2: [20, 50],
      value3: [20, 50],
      value4: 30,
      value5: [20, 50],
      value6: 30,
      value7: [20, 50],
      value8: 25,
      value9: 25,
      value10: 25
    };
  },
  methods: {
    format(val) {
      return "Progress: " + val + "%";
    },
    hideFormat() {
      return null;
    }
  }
};
